export const getPage = (state) => state.ui.page;
export const getShowBy = (state) => state.ui.showBy;
export const getCurrentType = (state) => state.ui.type;
export const getSearch = (state) => state.ui.search;
export const getLoading = (state) => state.ui.loading;
export const getError = (state) => state.ui.error;


